<template>
  <!-- <scroll-bar> -->
  <el-menu mode="vertical" class="sideBar full" :default-active="getSideBar()" :router="true">
    <el-menu-item v-for="(item,i) in sidebar" :index="item.path" :key="i+''">
      <i :class="['iconfont icon-font sidebar-icon','icon-'+item.name]"></i>
      <span class="sidebar-text">{{item.nameZh}}</span>
      <i class="el-icon-arrow-right icon-after"></i>
    </el-menu-item>
  </el-menu>
  <!-- </scroll-bar> -->
</template>

<script>
export default {
  name: 'Sidebar',
  data () {
    return {
    }
  },
  computed: {
    sidebar () {
      return this.$store.state.sidebar
    }
  },
  methods: {
    getSideBar () {
      let { path } = this.$route
      let active = ''
      this.$store.state.sidebar.map(function (item) {
        if (path === item.path) {
          active = item.path
        }
        if (item.subPage && item.subPage.includes(path)) {
          active = item.path
        }
      })
      return active
    }
  },
  mounted () {
    this.getSideBar()
  }
}
</script>

