import Vue from 'vue'
import Router from 'vue-router'

/**
 * webpack require context
 * url:https://webpack.docschina.org/api/module-methods/#require-context
 * **/
let openPages = importViews(require.context(`@/openViews`, false, /\.vue$/), '/')
let innerPages = importViews(require.context(`@/views`, true, /\.vue$/, 'lazy'), '/', true)
// 
function importViews (views, routerFrontPath = '', isLazy = false) {
  if (!views) return
  let routeParams
  routeParams = views.keys().map((key, i) => {
    // viewName : "./InstallPkg.vue"
    let viewPath = (key.split('.'))[1]
    let viewName = viewPath.replace('/', '')
    return ({
      path: `${routerFrontPath}${viewName}`,
      name: viewName,
      component: (isLazy ? () => { return views(key) } : views(key).default)
    })
  })
  return routeParams
}
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    { path: '/', redirect: '/login' },
    {
      path: '/',
      component: require(/* webpackChunkName: "layout" */ '@/layout/layout').default,
      children: [
        { path: '/', redirect: '/examination' }
      ].concat(innerPages)
    }
  ].concat(openPages).concat([
    { path: '*', redirect: '/404' }
  ])
})
