<template>
  <div class="login-container">
    <div class="loginWrap">
      <div class="login-logo-warp">
        <div class="login-inner">
          <img src="@/assets/images/logo.png">
          <p class="logo-explain" style="margin-top:35px;font-size:20px;"></p>
          <p class="logo-explain" style="font-size:16px">&emsp;&emsp;</p>
        </div>
      </div>
      <el-form class="login-form" :model="loginForm" status-icon ref="loginForm">
        <div class="title-container">
          <h3 class="title"></h3>
        </div>
        <el-form-item prop="account" verify empty-message="">
          <span class="icon-container">
            <i class="iconfont icon-user-line" style="font-size:20px"></i>
          </span>
          <el-input v-model="loginForm.account" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="password" verify empty-message="">
          <span class="icon-container">
            <i class="iconfont icon-password" style="font-size:20px"></i>
          </span>
          <el-input type="password" v-model="loginForm.password" placeholder=""
            @keyup.enter.native="submitLoginForm('loginForm')"></el-input>
        </el-form-item>
        <el-button type="primary" @click="submitLoginForm('loginForm')" style="width:100%;margin-top:10px;"></el-button>
        <p class="moreLink">

          <router-link to="/supplierApply"><span></span></router-link>

          <router-link to="/applyProgress"></router-link>

          <!-- <router-link to="/findPwd">
            <span class="findPwdLink"></span>
          </router-link> -->
        </p>
      </el-form>
    </div>
    <div class="footer">&emsp;&emsp;&emsp;&emsp;4006-138-638</div>
  </div>
</template>

<script>
import '@/styles/login.scss'
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        account: '',
        password: ''
      },
      secret_key: '',
      encryptPwd: ''
    }
  },
  beforeMount () {
    this.loadOther()
    if (process.env.NODE_ENV == 'development') {
      this.loginForm.account = 'admin'
      this.loginForm.password = '123456'
    }
  },
  methods: {
    loadOther () {
      if (!_.isEmpty(window.JSEncrypt)) {
        return
      }
      return Promise.all([
        _.loadJS('/static/js/base64.js'),
        _.loadJS('/static/js/jsencrypt.min.js')
      ])
    },
    loginTest () {
      this.$ajax.get('/open/loginTest', { account: 13567175139 })
        .then((res) => {
          console.log(res)
        })
        .catch((e) => {
          console.log(e)
        })
    },
    submitLoginForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let { account, password: loginPassword } = this.loginForm
          this.$ajax.post('/open/getSecretKey')
            .then((res) => {
              return res.result
            })
            .then((res) => {
              this.secret_key = res
              let encrypt = new JSEncrypt()
              encrypt.setPublicKey(this.secret_key)
              this.encryptPwd = encrypt.encrypt(this.loginForm.password)
              return this.$ajax.post('/open/login', { account: this.loginForm.account, loginPassword: this.encryptPwd })
            })
            .then((res) => {
              let turnTo
              // type 0 ,type 1 
              if (res.result.type == 1) {
                this.$store.commit('setUserInfo', res.result)
                console.log(this.$store.state)
                turnTo = () => this.$router.push({ path: '/examSupplier' })
              } else {
                this.$store.commit('setUserInfo', res.result)
                turnTo = () => this.$router.push({ path: '/front/home' })
              }
              this.$message.success('!', {
                duration: 1000
              })
              setTimeout(function () {
                turnTo()
              }, 1000)
            })
            .catch((e) => {
              console.error(e)
              this.$alert(e.msg || '', { confirmButtonText: '', type: 'error' })
            })
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

