<template>
  <el-upload class="upload-demo" :http-request="handleUpload" :action="url" :on-preview="handlePreview"
    :on-remove="handleRemove" :before-remove="beforeRemove" :on-exceed="handleExceed" :on-change="handleChange"
    :file-list="fileList">
    <el-button size="small" type="primary"></el-button>
    <a slot="tip" class="link" v-if="tplLink" :href="tplLink" :download="tplName+'.doc'" style="margin-left:15px">{{tplName}}</a>
    <!-- <div slot="tip" class="el-upload__tip">jpg/png500kb</div> -->
  </el-upload>
</template>

<script>
export default {
  props: {
    fileUrl: {
      require: true,
      default: ''
    },
    'tpl-name': {
      default: ''
    },
    'tpl-link': {
      default: ''
    }
  },
  model: {
    prop: 'fileUrl',
    event: 'fileUrlChange'
  },
  computed: {
    fileUrlLocal: {
      get: function () {
        return this['tpl-link']
      },
      set: function (value) {
        this.$emit('fileUrlChange', value)
      }
    }
  },
  data () {
    return {
      url: '/open/uploadImg',
      fileList: []
    }
  },
  methods: {
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    handleExceed (files, fileList) {
      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `)
    },
    handleChange (file, fileList) {
      this.fileList = [file]
    },
    beforeRemove (file, fileList) {
      return this.$confirm(` ${file.name}`).then(() => { this.fileUrlLocal = '' })
    },
    handleUpload (param, fileList) {
      let formData = new FormData()
      formData.append('file', param.file)
      this.$ajax.post(this.url, formData,
        { upload: true,
          onUploadProgress: function (e) {
            if (e.total > 0) {
              e.percent = e.loaded / e.total * 100
            }
            param.onProgress(e)
          }
        }
      )
        .then((res) => {
          param.onSuccess(res)
          this.fileUrlLocal = res.result
        })
        .catch((err) => {
          param.onSuccess(err)
        })
    }
  }
}
</script>
