<template lang="pug">
  //- .supplierApply 123
  -
    let formList = [
    { label:'',name:'name'},
    { label:'',name:'creditCode',maxLength:'18'},
    { label:'',name:'legalPerson'},
    { label:'',name:'identity',maxLength:'18',idCard:true},
    { label:'',name:'username',maxLength:'12'},
    { label:'',name:'password',type:'password'},
    { label:'',name:'secondPassword',type:'password'},
    { label:'',name:'businessContact'},
    { label:'',name:'businessContactPhone',maxLength:'11'},
    { label:'',name:'businessResponsible'},
    { label:'',name:'businessResponsiblePhone',maxLength:'11'},
    { label:'',name:'emergencyContact'},
    { label:'',name:'emergencyContactPhone',maxLength:'11'},
    { label:'',name:'codeImg',maxLength:"4"},
    ]
  -
    let formUploadList = [
    { label: '', name:"businessImg" },
    { label: '', name:"commitmentPage" ,link:"/static/downloads/cx.doc"},
    { label: '', name:"authorizePage" ,link:"/static/downloads/fd.doc"},
    { label: '', name:"appointPage" ,link:"/static/downloads/zr.doc"},
    { label: '', name:"systemDesc" },
    ]

  layout( :showFooter="true" :showSidebar="false" )
    .supplierApply
      h1 
      .form-wrap
        el-form( label-position="right" label-width="160px" :model="form" ref="form")
          el-form-item( label="" verify prop="companyArea")
            el-select( placeholder="" style="width:100%" v-model="form.companyArea")
              el-option(label="" value="1")
              el-option(label="" value="0")

          el-form-item( label="" verify prop="contactType")
            el-select( placeholder="" style="width:100%" v-model="form.contactType")
              //- el-option(label="" value="1")
              el-option(label="" value="2")
              el-option(label="app" value="3")

          each formItem in formList
            if formItem.label == ''
              el-form-item(label=formItem.label prop=formItem.name :verify="verifyPassword" :watch="form.password")
                 el-input( placeholder=`${formItem.label}` v-model=`form.${formItem.name}` )&attributes(formItem)
            else
              el-form-item(label=formItem.label verify prop=formItem.name)
                el-input( placeholder=`${formItem.label}` v-model=`form.${formItem.name}` )&attributes(formItem)
                  if formItem.label == ''
                    template(slot="append")
                      img.codeImg(:src="codeImg" @click="changeImage")

          each uploadItem in formUploadList
            el-form-item(label=uploadItem.label verify prop=uploadItem.name empty-message=`${uploadItem.label}` )
              if uploadItem.link
                el-input( style="display:none" placeholder=`${uploadItem.label}` v-model=`form.${uploadItem.name}` )
                su-upload(v-model=`form.${uploadItem.name}`, tpl-name=uploadItem.label tpl-link=uploadItem.link)
              else
                el-input( style="display:none" placeholder=`${uploadItem.label}` v-model=`form.${uploadItem.name}` )
                su-upload(v-model=`form.${uploadItem.name}`)

          el-form-item(style="margin-top:-15px,color:#f39801;")
            .upload-tip(style="color:#f39801;") jpg

          el-form-item
            <el-button class="su-button" @click="$router.push('./login')"></el-button>
            <el-button class="su-button" @click="handleSubmit" type="primary"></el-button>
</template>

<script>
// import layout from '@/layout/layout'
import suUpload from '@/components/su-upload'

export default {
  components: { suUpload },
  data () {
    return {
      form: {},
      upload: {},
      codeImg: ''
    }
  },
  beforeMount () {
    this.changeImage()
  },
  methods: {
    verifyPassword (rule, val, callback) {
      console.log(val)
      if (val !== this.form.password) callback(Error('!'))
      else callback()
    },
    handleSubmit () {
      this.$refs.form.validate((valid) => {
        let _this = this
        // _this.$router.push('/applyResult')
        if (!valid) {
          return
        }
        // todo img check
        this.$ajax.post('/open/register', this.form)
          .then((res) => {
            this.$message.success('')
            setTimeout(() => {
              _this.$router.push('/applyResult')
            }, 2000)
          })
          .catch((e) => {
            this.$message.error(e.msg || '')
          })
      })
    },
    changeImage () {
      this.$ajax.get('/open/createCodeImg', { responseType: 'blob' })
        .then((res) => {
          let reader = new FileReader()
          reader.onload = (e) => {
            this.codeImg = e.target.result
          }
          reader.onerror = (e) => {
            throw e
          }
          reader.readAsDataURL(res)
          // this.codeImg = res
        })
        .catch((e) => {
          this.$message.error('!')
        })
    }
  }
}
</script>
<style lang="scss">
.supplierApply {
  width: 100%;
  min-width: 1024px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
  h1 {
    display: block;
    width: 80%;
    text-align: center;
    padding: 0 0 20px;
    border-bottom: 2px solid #d8d8d8;
  }
  .form-wrap {
    width: 600px;
    margin-left: -140px;
  }
  .codeImg {
    cursor: pointer;
  }
  .su-button.el-button {
    width: 200px;
  }
}
</style>
