import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersist from 'vuex-persist'
import ajax from '@/utils/Axios'
import _ from '@/utils/utils'
const vuexLocal = new VuexPersist({ storage: window.localStorage })
Vue.use(Vuex)
export default new Vuex.Store({
  plugins: [vuexLocal.plugin],
  state: {
    userInfo: {},
    companyInfo: {},
    sidebar: [

    ],
    userSidebar: [
      { path: '/front/home', name: 'home', nameZh: '' },
      { path: '/front/testDocking', name: 'testDocking', nameZh: '' },
      {
        path: '/front/prodDocking',
        name: 'prodDocking',
        nameZh: '',
        subPage: ['/front/createProd']
      },
    ],
    adminSidebar: [
      { path: '/examSupplier', name: 'examSupplier', nameZh: '' },
      { path: '/examTest', name: 'examTest', nameZh: '' },
      { path: '/examProject', name: 'examProject', nameZh: '' },
    ],
    areaList: []
  },
  getters: {

  },
  mutations: {
    getPageBelong: (state, pageName) => {
      let sidebarName = ''
      state.sidebar.map(function (item) {
        if (pageName === item.name) {
          sidebarName = item.name
        }
        if (utils.isEmpty(item.subPage)) {
          return
        }
        if (item.subPage.includes(pageName)) {
          sidebarName = item.name
        }
      })
      return sidebarName
    },
    setUserInfo: (state, userInfo) => {
      // type 0 ,type 1 
      if (userInfo.type == 0) {
        state.sidebar = state.userSidebar
      }
      if (userInfo.type == 1) {
        state.sidebar = state.adminSidebar
      }
      state.userInfo = userInfo
    },
    setAreaList: (state, areaList) => {
      state.areaList = areaList
    },
    setCompanyInfo: (state, companyInfo) => {
      state.companyInfo = companyInfo
    }
  },
  actions: {
  }
})
